export default {
  namespaced: true,
  state: {
    navlist: {
      root: {
        ruName: "Главная",
        to: "/home",
        icon: "graph"
      },
      taskList: {
        ruName: "Список задач",
        to: "/calendar",
        icon: "calendar"
      },
      messages: {
        ruName: "Сообщения",
        to: "/messagesList",
        icon: "mail"
      },
      newTask: {
        ruName: "Поставить новую задачу",
        to: "/newTasks",
        icon: "new-tasks"
      },
      Docs: {
        ruName: "Документы",
        to: "/docs",
        icon: "file"
      }
    },
    additionalNavList: {
      user: {
        ruName: "Пользователь",
        to: "/profile",
        icon: "user"
      },
      output: {
        ruName: "Выйти",
        to: "",
        icon: "out",
        event: "outUser"
      }
    },
    mapDocumentTypeSort: {
      Устав: 0,
      Приказ: 1,
      Протокол: 2,
      Журнал: 3,
      Форма: 4,
      Акт: 5,
      Табель: 6,
      "": -1
    },
    mapTypeSort: {
      Exel: 0,
      Word: 1
    },
    theader: {
      name: {
        name: "Название",
        sort: "default"
      },
      documentType: {
        name: "Тип документа",
        sort: "default"
      },
      type: {
        name: "Файл",
        sort: "default"
      },
      date: {
        name: "Дата публикации",
        sort: "default"
      },
      update: {
        name: "Дата изменения",
        sort: "default"
      }
    },
    news: [
      {
        urlToImage:
          "https://cdn25.img.ria.ru/images/07e4/02/06/1564361698_0:0:3074:1729_600x0_80_0_0_72df6dbfe4dc6bf1c0281c8032729918.jpg.webp",
        title:
          "Лукашенко: в кампании Бабарико и Цепкало были 'закачаны деньги' из России ",
        description:
          "Lorem ipsum dolor sit amet, consectetur adipisicing elit. Quod, debitis magni. Eius vero doloremque ex voluptates, illo rerum molestias. Dolor quidem nisi nemo perferendis et nostrum esse iure consectetur magni.",
        url: "https://ria.ru/20200806/1575464685.html"
      },
      {
        urlToImage:
          "https://cdn25.img.ria.ru/images/07e4/08/06/1575464598_0:0:640:360_600x0_80_0_0_c13306e20e005d78f5bdb3f04027a601.jpg.webp",
        title: "Ефремова привезли домой после заседания в суде",
        description:
          "Lorem ipsum dolor sit amet consectetur, adipisicing elit. Repellat, consequatur.",
        url: "https://ria.ru/20200806/1575465080.html"
      }
    ]
  },
  getters: {
    getNavlist(state, _, __, getters) {
      let classNameMessage = getters["messages/getClassNameforNavList"];
      let countOfUnreadMessages = getters["messages/countOfUnreadMessages"];
      let classNameTasks = getters["tasks/getClassNameforNavList"];
      let getCountOfUnreadTasks = getters["tasks/getCountOfUnreadTasks"];
      state.navlist.messages.className = classNameMessage;
      state.navlist.taskList.className = classNameTasks;
      if (countOfUnreadMessages)
        state.navlist.messages.dataAttribute = countOfUnreadMessages;
      if (getCountOfUnreadTasks)
        state.navlist.taskList.dataAttribute = getCountOfUnreadTasks;
      if (!getters["isSizeDesktop"])
        return Object.values({ ...state.navlist, ...state.additionalNavList });
      return Object.values(state.navlist);
    },
    getMapTypeDocSort: state => state.mapTypeDocSort,
    getMapTypeSort: state => state.mapTypeSort
  }
};
